
#app {
  font-family: 'Lato', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  min-width: 600px;
  margin: 60px auto;
  width: 60%;
}
@media screen 
  and (max-width: 40.000em){ /*960px*/
#app { 
      margin: auto;
      min-width: 385px;
}
}



